@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/Roboto.woff") format("woff"), url("../resources/fonts/Roboto.woff2") format("woff2"), url("../resources/fonts/Roboto.ttf") format("ttf"), url("../resources/fonts/Roboto.svg") format("svg");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1,
h2 {
  font-weight: 700;
}

h2 {
  font-size: 30px;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

body {
  background-color: #3b3b3b;
  font-family: "Eczar", sans-serif;
  font-weight: 400;
}

main {
  max-width: 480px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-image: url(../images/main-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.screen {
  flex: 1 0 auto;
  padding: 50px 0;
}

.screen__btn {
  width: 250px;
  height: 50px;
  margin-bottom: 30px;
  background-color: #fff;
  border: 3px solid #c22626;
  border-radius: 12px;
  font-size: 18px;
  color: #c22626;
  text-transform: uppercase;
  transition: all 0.3s ease;
  cursor: pointer;
}
.screen__btn:last-child {
  margin-bottom: 0;
}
.screen__btn:hover {
  box-shadow: 4px 6px 15px #b11f1f;
  background-color: #c22626;
  color: #fff;
}
@media (max-width: 479.98px) {
  .screen__btn:hover {
    box-shadow: none;
    background-color: #fff;
    color: #c22626;
  }
}

.btn__back i {
  margin-right: 5px;
}

:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-primary: #e3e3e3;
  --grid-gap: 30px;
}

.header {
  flex: 0 1 auto;
  max-height: 100px;
  background-color: rgba(0, 0, 0, 0.5);
}
.header__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0;
}
.header__logo-icon {
  display: block;
  width: 50px;
  height: 50px;
}
.header__logo-title {
  display: block;
  margin-left: 20px;
  font-family: "Eczar", sans-serif;
  font-weight: 600;
  font-size: 24px;
  color: white;
  transition: color 0.3s ease;
}
.header__logo-title:hover {
  color: #c22626;
}

.footer {
  flex: 0 1 auto;
  max-height: 100px;
  background-color: rgba(0, 0, 0, 0.5);
}
.footer__body {
  padding: 25px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__privacy {
  font-size: 18px;
  color: #fff;
}
.footer__privacy-link {
  color: #fff;
  transition: color 0.3s ease;
}
.footer__privacy-link:hover {
  color: #c22626;
}
.footer__author {
  font-size: 22px;
  color: #fff;
  text-align: right;
}

.screen-main__header {
  display: block;
  margin-bottom: 50px;
  color: #fff;
  text-align: center;
}
.screen-main__title {
  font-size: 36px;
  line-height: 38px;
}
@media (max-width: 339.98px) {
  .screen-main__title {
    font-size: 26px;
    line-height: 28px;
  }
}
.screen-main__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.screen-desc__header {
  margin-bottom: 20px;
}
.screen-desc__title {
  line-height: 36px;
  color: #fff;
  text-align: center;
}
.screen-desc__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.screen-desc__info {
  margin-bottom: 50px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  box-shadow: 5px 5px 10px rgba(255, 255, 255, 0.7);
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #000;
}
@media (max-width: 339.98px) {
  .screen-desc__info {
    font-size: 16px;
    line-height: 18px;
  }
}
.screen-desc__info-desc {
  margin-bottom: 20px;
}
.screen-desc__info-desc a {
  color: inherit;
}
.screen-desc__info-list__item {
  position: relative;
  padding-left: 15px;
  margin-bottom: 10px;
}
.screen-desc__info-list__item:last-child {
  margin-bottom: 0;
}
.screen-desc__info-list__item::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 5px;
  height: 5px;
  background-color: #000;
  border-radius: 50%;
}

.screen-author__header {
  margin-bottom: 20px;
}
.screen-author__title {
  line-height: 36px;
  color: #fff;
  text-align: center;
}
.screen-author__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.screen-author__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
}

.screen-play {
  position: relative;
  padding-bottom: 0;
}
.screen-play__question {
  position: relative;
  padding: 20px;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 15px;
}
.screen-play__hieroglyph {
  font-size: 18px;
  line-height: 26px;
  text-align: center;
}
.screen-play__hieroglyph strong {
  font-size: 22px;
  color: #c22626;
}
.screen-play__timer {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 12px;
  font-weight: 700;
  color: #c22626;
}
.screen-play__answers {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
}
.screen-play__answers-score {
  position: absolute;
  left: 10px;
  bottom: 10px;
  font-size: 20px;
}
@media (max-width: 479.98px) {
  .screen-play__answers-score {
    position: initial;
  }
}
.screen-play__answers-score_right {
  color: #36e05b;
  margin-bottom: 10px;
}
.screen-play__answers-score_wrong {
  color: #c22626;
}

.screen-final__score {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  font-size: 20px;
  font-weight: 600;
}
@media (max-width: 339.98px) {
  .screen-final__score {
    font-size: 18px;
  }
}
.screen-final__score-right {
  color: #36e05b;
}
.screen-final__score-wrong {
  color: #c22626;
}
.screen-final__btns {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}